import React, { useState, useEffect, useCallback } from 'react';
import { SaintGridPetSystem } from './index';
import { PetState, EmotionType } from './types';
import { StrategyConfigPanel } from './modules/StrategyConfigUI/StrategyConfigPanel';

interface PetSystemAppState {
  currentState: PetState;
  currentEmotion: EmotionType;
  isSystemReady: boolean;
  pluginStatus: string;
  rhythmMode: string;
  lastBehavior: string;
  showStrategyPanel: boolean;
}

const PetSystemApp: React.FC = () => {
  const [petSystem, setPetSystem] = useState<SaintGridPetSystem | null>(null);
  const [appState, setAppState] = useState<PetSystemAppState>({
    currentState: PetState.Idle,
    currentEmotion: EmotionType.Calm,
    isSystemReady: false,
    pluginStatus: '',
    rhythmMode: 'steady',
    lastBehavior: '',
    showStrategyPanel: false
  });

  // 
  useEffect(() => {
    const initPetSystem = async () => {
      try {
        console.log(' Initializing SaintGrid Pet System...');
        const system = new SaintGridPetSystem();
        await system.start();
        
        setPetSystem(system);
        setAppState(prev => ({ ...prev, isSystemReady: true }));
        
        console.log(' Pet System UI Integration Completed');
        
        //  Electron 
        if (window.electronAPI) {
          await window.electronAPI.onPetStateChange({
            state: PetState.Idle,
            emotion: EmotionType.Calm,
            timestamp: Date.now()
          });
        }
      } catch (error) {
        console.error(' Pet System Initialization Failed:', error);
      }
    };

    initPetSystem();

    // 
    return () => {
      if (petSystem) {
        petSystem.stop().catch(console.error);
      }
    };
  }, []);

  //  -  hover 
  const handleMouseEnter = useCallback(async () => {
    if (!petSystem) return;

    try {
      console.log(' Mouse Enter - Triggering Hover State');
      await petSystem.onLeftClick(); //  hover
      
      setAppState(prev => ({
        ...prev,
        currentState: PetState.Hover,
        currentEmotion: EmotionType.Curious,
        lastBehavior: 'hover_enter'
      }));

      //  Electron
      if (window.electronAPI) {
        await window.electronAPI.onPetStateChange({
          state: PetState.Hover,
          emotion: EmotionType.Curious,
          action: 'mouse_enter',
          timestamp: Date.now()
        });
      }
    } catch (error) {
      console.error(' Hover state transition failed:', error);
    }
  }, [petSystem]);

  //  -  idle 
  const handleMouseLeave = useCallback(async () => {
    if (!petSystem) return;

    try {
      console.log(' Mouse Leave - Returning to Idle State');
      await petSystem.onMouseLeave();
      
      setAppState(prev => ({
        ...prev,
        currentState: PetState.Idle,
        currentEmotion: EmotionType.Calm,
        lastBehavior: 'hover_exit'
      }));

      //  Electron
      if (window.electronAPI) {
        await window.electronAPI.onPetStateChange({
          state: PetState.Idle,
          emotion: EmotionType.Calm,
          action: 'mouse_leave',
          timestamp: Date.now()
        });
      }
    } catch (error) {
      console.error(' Idle state transition failed:', error);
    }
  }, [petSystem]);

  //  -  awaken 
  const handleLeftClick = useCallback(async () => {
    if (!petSystem) return;

    try {
      console.log(' Left Click - Triggering Awaken State');
      await petSystem.onLeftClick();
      
      setAppState(prev => ({
        ...prev,
        currentState: PetState.Awaken,
        currentEmotion: EmotionType.Excited,
        lastBehavior: 'awaken_click',
        pluginStatus: 'screenshot_ready'
      }));

      //  Electron
      if (window.electronAPI) {
        await window.electronAPI.onPetBehaviorTrigger({
          state: PetState.Awaken,
          emotion: EmotionType.Excited,
          action: 'left_click',
          plugin: 'screenshot',
          timestamp: Date.now()
        });
      }
    } catch (error) {
      console.error(' Awaken state transition failed:', error);
    }
  }, [petSystem]);

  //  -  control 
  const handleRightClick = useCallback(async (event: React.MouseEvent) => {
    event.preventDefault();
    if (!petSystem) return;

    try {
      console.log(' Right Click - Triggering Control State');
      await petSystem.onRightClick();
      
      setAppState(prev => ({
        ...prev,
        currentState: PetState.Control,
        currentEmotion: EmotionType.Focused,
        lastBehavior: 'control_menu',
        pluginStatus: 'note_ready'
      }));

      //  Electron
      if (window.electronAPI) {
        await window.electronAPI.onPetBehaviorTrigger({
          state: PetState.Control,
          emotion: EmotionType.Focused,
          action: 'right_click',
          plugin: 'note',
          timestamp: Date.now()
        });
      }
    } catch (error) {
      console.error(' Control state transition failed:', error);
    }
  }, [petSystem]);

  // 
  const toggleStrategyPanel = useCallback(() => {
    setAppState(prev => ({
      ...prev,
      showStrategyPanel: !prev.showStrategyPanel
    }));
  }, []);

  //  CSS 
  const getStateClassName = () => {
    const stateClass = `pet-state-${appState.currentState.toLowerCase()}`;
    const emotionClass = `emotion-${appState.currentEmotion.toLowerCase()}`;
    return `pet-bowl ${stateClass} ${emotionClass}`;
  };

  // 
  const getStateText = () => {
    switch (appState.currentState) {
      case PetState.Idle: return ' ';
      case PetState.Hover: return ' ';
      case PetState.Awaken: return ' ';
      case PetState.Control: return ' ';
      default: return ' ';
    }
  };

  // 
  const getEmotionText = () => {
    switch (appState.currentEmotion) {
      case EmotionType.Calm: return ' ';
      case EmotionType.Curious: return ' ';
      case EmotionType.Focused: return ' ';
      case EmotionType.Happy: return ' ';
      case EmotionType.Excited: return ' ';
      default: return ' ';
    }
  };

  return (
    <div className="pet-container">
      {/*  */}
      <button
        onClick={toggleStrategyPanel}
        style={{
          position: 'absolute',
          top: '10px',
          right: '10px',
          padding: '8px 12px',
          backgroundColor: appState.showStrategyPanel ? '#dc3545' : '#007bff',
          color: 'white',
          border: 'none',
          borderRadius: '4px',
          cursor: 'pointer',
          fontSize: '12px',
          fontWeight: 'bold',
          zIndex: 1000
        }}
      >
        {appState.showStrategyPanel ? ' ' : ' '}
      </button>

      {/*  */}
      {appState.showStrategyPanel && (
        <div style={{
          position: 'absolute',
          top: '50px',
          right: '10px',
          width: '600px',
          maxHeight: 'calc(100vh - 70px)',
          overflow: 'auto',
          backgroundColor: 'white',
          border: '1px solid #ddd',
          borderRadius: '8px',
          boxShadow: '0 4px 8px rgba(0,0,0,0.2)',
          zIndex: 999
        }}>
          <StrategyConfigPanel />
        </div>
      )}

      {/*  */}
      <div className="state-indicator">
        {getStateText()}
      </div>
      
      {/*  */}
      <div className="emotion-indicator">
        {getEmotionText()}
      </div>

      {/*  */}
      <div
        className={getStateClassName()}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onClick={handleLeftClick}
        onContextMenu={handleRightClick}
        title={`: ${appState.currentState} | : ${appState.currentEmotion}`}
      >
        {/*  */}
        <div style={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          textAlign: 'center',
          color: 'white',
          fontWeight: 'bold',
          textShadow: '0 2px 4px rgba(0,0,0,0.5)',
          pointerEvents: 'none'
        }}>
          <div style={{ fontSize: '48px', marginBottom: '8px' }}>
            
          </div>
          <div style={{ fontSize: '14px', opacity: 0.9 }}>
            SaintGrid
          </div>
        </div>
      </div>

      {/*  */}
      <div className={`plugin-indicator ${appState.pluginStatus ? 'show' : ''}`}>
        {appState.pluginStatus && ` ${appState.pluginStatus}`}
      </div>

      {/*  () */}
      {window.electronAPI?.isDev && (
        <div style={{
          position: 'absolute',
          bottom: '10px',
          left: '10px',
          background: 'rgba(0,0,0,0.8)',
          color: 'white',
          padding: '8px',
          borderRadius: '4px',
          fontSize: '10px',
          maxWidth: '200px',
          pointerEvents: 'none'
        }}>
          <div>: {appState.isSystemReady ? ' ' : ' '}</div>
          <div>: {appState.currentState}</div>
          <div>: {appState.currentEmotion}</div>
          <div>: {appState.lastBehavior}</div>
          <div>: {appState.rhythmMode}</div>
        </div>
      )}
    </div>
  );
};

export default PetSystemApp;
