/**
 * SaintGrid  - 
 * 
 */

// 
export enum PetState {
  Idle = 'idle',           //  - 
  Hover = 'hover',         //  - 
  Awaken = 'awaken',       //  - 
  Control = 'control'      //  - 
}

// 
export enum EmotionType {
  Happy = 'happy',
  Calm = 'calm',
  Excited = 'excited',
  Curious = 'curious',
  Sleepy = 'sleepy',
  Focused = 'focused'
}

// AI 
export enum AIProvider {
  // 
  OpenAI = 'openai',
  Claude = 'claude',
  
  // 
  Tongyi = 'tongyi',
  Doubao = 'doubao',
  Wenxin = 'wenxin',
  
  // 
  GPT4 = 'gpt4',
  Gemini = 'gemini',
  LLaMA = 'llama'
}

// 
export interface UserIntent {
  type: string;
  confidence: number;
  parameters: Record<string, any>;
  rawInput: string;
  timestamp: number;
}

//  - 
export interface PluginContext {
  currentState: PetState;
  emotion: EmotionContext;
  userPreferences?: Record<string, any>;
  stateHistory?: PetState[];
  interaction?: {
    type: 'passive' | 'active';  //  vs 
    trigger: 'state_change' | 'user_intent' | 'heartbeat' | 'manual';
    timestamp: number;
  };
}

// 
export type PluginHookType = 'onStateChanged' | 'onEmotionChanged' | 'onUserInteraction' | 'onHeartbeat';

//  - 
export interface IPlugin {
  id: string;
  name: string;
  version: string;
  description: string;
  supportedIntents: string[];
  
  // 
  capabilities?: {
    stateAware: boolean;      // 
    emotionAware: boolean;    // 
    contextAware: boolean;    // 
    supportedHooks: PluginHookType[]; // 
  };
  
  // 
  initialize(): Promise<void>;
  execute(intent: UserIntent, context: any): Promise<PluginResponse>;
  destroy(): Promise<void>;
  
  //  - 
  trigger?(state: PetState, emotion: EmotionContext, context?: PluginContext): Promise<PluginResponse>;
  
  // 
  onStateChanged?(oldState: PetState, newState: PetState, emotion: EmotionContext, context?: PluginContext): Promise<PluginResponse>;
  onEmotionChanged?(oldEmotion: EmotionType, newEmotion: EmotionType, context?: PluginContext): Promise<PluginResponse>;
}

// 
export interface PluginResponse {
  success: boolean;
  data: any;
  message?: string;
  emotion?: EmotionType;
  nextState?: PetState;
}

// 
export interface MemoryData {
  id: string;
  type: 'conversation' | 'behavior' | 'preference' | 'context';
  content: any;
  timestamp: number;
  importance: number; // 1-10, 
  tags: string[];
}

// 
export interface EmotionContext {
  currentEmotion: EmotionType;
  intensity: number; // 0-1, 
  duration: number;  // 
  triggers: string[]; // 
  history: EmotionHistory[];
}

export interface EmotionHistory {
  emotion: EmotionType;
  timestamp: number;
  cause: string;
}

// 
export interface PetBrainConfig {
  defaultState: PetState;
  defaultEmotion: EmotionType;
  memoryLimit: number;
  aiProviders: AIProvider[];
  plugins: string[];
}
